.listWrap {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
}

.confItem {
  display: flex;
  margin-bottom: 40px;
}

.confAvatar {
  width: 280px;
  /* height: 479px; */
  padding: 20px;
  text-align: center;
  align-items: center;
  border-radius: 5px;
  background: #fff;
  box-shadow: 0px 2px 10px 0px rgba(67, 96, 124, 0.25);
}

.confAvatarImg {
  width: 200px;
  height: 200px;
  flex-shrink: 0;
  border-radius: 5px;
  border: 1px solid #ffe1e1;
  margin: 0 auto 20px;
}

.confAvatarName {
  color: #43607c;
  font-family: Verdana;
  font-size: 18px;
  font-weight: 700;
  line-height: 2.1;
}

.confAvatarComments {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
  color: #a63a50;
  text-decoration-line: underline;
  margin-bottom: 20px;
}

.confAvatarInfo {
  color: #43607c;
  font-family: Verdana;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
  margin-bottom: 22px;
}

.recomendationBtn {
  width: 120px;
  height: 40px;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #8e4a4e;
  background-color: #fff5f7;
}

.confAvatarButtonList {
  display: flex;
  align-items: center;
  gap: 5px;
}

.confAvatarButtonList button {
  margin: 0 auto;
  padding: 2px 10px;
  gap: 30px;
  border-radius: 30px;
  background-color: #fff2f2;
  color: #a63a50;
  font-family: Verdana;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.7;
}

.ConfInfo {
  margin-left: 20px;
}

.confSubInfo {
  display: flex;
  width: 1150px;
  height: 35px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.confSubInfo h2 {
  color: #011936;
  font-family: "Yeseva One";
  font-size: 24px;
  font-weight: 400;
  line-height: 1.2;
}

.confBtnList {
  display: flex;
  column-gap: 20px;
  align-items: center;
  margin-bottom: 40px;
}

.confBtnList div {
  display: flex;
}

.confViewBtn {
  width: 158px;
  height: 35px;
  padding: 5px 20px;
  border-radius: 30px;
  background: #dcebf8;
  color: var(--textColor);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.7;
}

.confNextBtn {
  display: flex;
  width: 24px;
  height: 24px;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.confCakeItem {
  display: flex;
  width: 280px;
  height: 424px;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 10px;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0px 2px 5px 0px rgba(67, 96, 124, 0.15);
}

.confCakeItemImg {
  width: 280px;
  height: 300px;
  flex-shrink: 0;
  border-radius: 5px;
}

.confCakeItemWrap {
  display: flex;
  padding: 0px 10px 15px 10px;
  /* padding: 15px; */
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  height: 114px;
}

.confCakeItemWrap h2 {
  color: var(--Black, #011936);
  font-family: Verdana;
  font-size: 18px;
  font-weight: 700;
  line-height: 2.1;
}

.confCakeItemWrap h3 {
  color: #011936;
  font-family: Verdana;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
}

.confCakeItemWrap p {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #84a6c2;
  font-family: Verdana;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
}

